import React, { useState, useMemo, useRef, useEffect } from 'react';

const TimelineComponent = ({ events, startDate, endDate }) => {
  const [zoomLevel, setZoomLevel] = useState(1);
  const marginPercentage = 5; // Percentuale di margine ai lati della timeline
  const offsetPercentage = 2; // Offset per spostare "Inizio" e "Fine" verso l'interno

  const getPositionPercentage = (date) => {
    const totalDuration = endDate - startDate;
    const eventDate = new Date(date) - startDate;
    return (eventDate / totalDuration) * (100 - 2 * marginPercentage) + marginPercentage;
  };

  const handleZoomChange = (event) => setZoomLevel(Number(event.target.value));

  // Calcola la larghezza totale della timeline in percentuale con il livello di zoom
  const timelineWidth = `${100 * zoomLevel}%`;

  return (
    <div style={{ padding: '16px' }}>
      <div style={{ marginBottom: '10px', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        <label>Zoom: </label>
        <input 
          type="range" 
          min="0.5" 
          max="3" 
          step="0.1" 
          value={zoomLevel} 
          onChange={handleZoomChange} 
          style={{ marginLeft: '10px' }}
        />
      </div>

      <div style={{ 
        position: 'relative', 
        width: '100%', 
        height: '200px', 
        margin: '20px 0', 
        overflowX: 'auto',  // Abilita lo scrolling orizzontale
        whiteSpace: 'nowrap',  // Evita che gli elementi vadano a capo
        border: '1px solid #ddd',  // Aggiungi un bordo per vedere i limiti della timeline
      }}>
        <div style={{ 
          position: 'relative', 
          width: timelineWidth,  // Usa la larghezza calcolata in base allo zoom
          height: '100px', 
          borderBottom: '2px solid #007bff', 
          display: 'flex', 
          alignItems: 'center',
          paddingLeft: `${marginPercentage}%`, // Aggiunge margine a sinistra
          paddingRight: `${marginPercentage}%`, // Aggiunge margine a destra
        }}>
          {/* Aggiunta di "Inizio" con offset verso l'interno */}
          <div style={{ 
            position: 'absolute', 
            left: `${offsetPercentage}%`, 
            transform: 'translateX(-50%)',
            textAlign: 'center',
            fontSize: '10px',
            fontWeight: 'bold',
            marginTop: '80px',
          }}>
            Inizio
          </div>

          {/* Aggiunta degli eventi */}
          {events.map((event, index) => (
            <div key={index} style={{ 
              position: 'absolute', 
              left: `${getPositionPercentage(event.date)}%`, 
              transform: 'translateX(-50%)',
              textAlign: 'center',
            }}>
              <div style={{ 
                fontSize: '10px',  
                fontWeight: 'normal',
                writingMode: 'vertical-rl', 
                transform: 'rotate(180deg)',
                marginTop: '20px'  
              }}>{event.label}</div>

              <div style={{ 
                height: '50px',  
                borderLeft: '2px solid #007bff', 
                marginBottom: '0px' 
              }}></div>
            </div>
          ))}

          {/* Aggiunta di "Fine" con offset verso l'interno */}
          <div style={{ 
            position: 'absolute', 
            right: `${offsetPercentage}%`, 
            transform: 'translateX(50%)',
            textAlign: 'center',
            fontSize: '10px',
            fontWeight: 'bold',
            marginTop: '80px',
          }}>
            Fine
          </div>
        </div>
        <div style={{ 
          position: 'relative', 
          width: timelineWidth,  // Usa la larghezza calcolata in base allo zoom
          display: 'flex', 
          justifyContent: 'space-between', 
          marginTop: '0px',  // Aggiunge spazio sopra le date per separarle dalla timeline
          fontSize: '10px',
          paddingLeft: `${marginPercentage}%`, // Aggiunge margine a sinistra
          paddingRight: `${marginPercentage}%`, // Aggiunge margine a destra
        }}>
          {/* Aggiunta delle date */}
          {events.map((event, index) => (
            <div key={index} style={{ 
              position: 'absolute', 
              left: `${getPositionPercentage(event.date)}%`, 
              transform: 'translateX(-50%)',
              textAlign: 'center',
            }}>
              <div style={{
                writingMode: 'vertical-rl',  // Scrive la data in verticale
                transform: 'rotate(180deg)',  // Ruota il testo per renderlo leggibile
                marginTop: '4px',  // Margine superiore per aggiungere spazio tra le date e la fine della linea
                fontSize: '8px',
                fontWeight: 'normal',
              }}>
                {new Date(event.date).toLocaleDateString('it-IT', { day: '2-digit', month: '2-digit', year: 'numeric' })}
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

// Componente principale del Gantt Chart
const GanttChartComponent = ({ tableRows, onUpdateTask }) => {
  // Funzione per calcolare la durata in giorni tra due date
  const getDateDiff = (start, end) => {
    const diffTime = Math.abs(new Date(end) - new Date(start));
    return Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  };

  // Stato per le attività visualizzate nel grafico
  const [tasks, setTasks] = useState(tableRows.map((row, index) => ({
    id: row.id,
    displayId: index + 1,
    name: row.nomeAttivita,
    start: row.dataInizio,
    end: row.dataFine,
    duration: getDateDiff(row.dataInizio, row.dataFine)
  })));

  // Stati per la gestione dell'editing e dello zoom
  const [editingTask, setEditingTask] = useState(null);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [zoomLevel, setZoomLevel] = useState(30);

  // Stato e ref per la gestione dello scroll
  const [scrollPosition, setScrollPosition] = useState(0);
  const timelineRef = useRef(null);

  // Funzione per formattare le date in formato italiano
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('it-IT', { day: '2-digit', month: '2-digit', year: 'numeric' });
  };

  // Funzione per calcolare il numero della settimana di una data
  const getWeekNumber = (date) => {
    const d = new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate()));
    const dayNum = d.getUTCDay() || 7;
    d.setUTCDate(d.getUTCDate() + 4 - dayNum);
    const yearStart = new Date(Date.UTC(d.getUTCFullYear(), 0, 1));
    return Math.ceil((((d - yearStart) / 86400000) + 1) / 7);
  };

  // Uso di useMemo per calcolare in modo efficiente le liste di giorni, settimane, mesi, anni
  const { startDate, endDate, daysList, weeksList, monthsList, yearsList } = useMemo(() => {
    const allDates = tasks.flatMap(task => [new Date(task.start), new Date(task.end)]);
    const startDate = new Date(Math.min(...allDates));
    const endDate = new Date(Math.max(...allDates));
    endDate.setMonth(endDate.getMonth() + 1, 0);
    const daysList = [];
    const weeksList = [];
    const monthsList = [];
    const yearsList = [];
    let currentDate = new Date(startDate);

    while (currentDate <= endDate) {
      daysList.push(new Date(currentDate));
      if (currentDate.getDay() === 0) weeksList.push(new Date(currentDate));
      if (currentDate.getDate() === 1) monthsList.push(new Date(currentDate));
      if (currentDate.getMonth() === 0 && currentDate.getDate() === 1) yearsList.push(new Date(currentDate));
      currentDate.setDate(currentDate.getDate() + 1);
    }

    return { startDate, endDate, daysList, weeksList, monthsList, yearsList };
  }, [tasks]);

  const totalDays = getDateDiff(startDate, endDate) + 1;

  // Funzione per gestire lo zoom in e out tramite un cursore
  const handleZoomChange = (event) => setZoomLevel(Number(event.target.value));

  const isWeekView = zoomLevel < 15;

  // Rendering delle barre che rappresentano i mesi
  const renderMonths = () => {
    return monthsList.map((month, index) => {
      let nextMonth;
      if (index === monthsList.length - 1) {
        nextMonth = new Date(endDate);
      } else {
        nextMonth = new Date(monthsList[index + 1]);
      }

      const startOffset = getDateDiff(startDate, month) * zoomLevel;
      const width = (getDateDiff(month, nextMonth)) * zoomLevel;

      return (
        <div
          key={index}
          style={{
            position: 'absolute',
            left: `${startOffset}px`,
            width: `${width}px`,
            height: '20px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRight: '1px solid #ccc',
            fontWeight: 'bold',
            fontSize: '0.6rem',
            boxSizing: 'border-box',
            zIndex: 1,
          }}
        >
          {month.toLocaleString('it-IT', { month: 'long', year: 'numeric' })}
        </div>
      );
    });
  };

  // Rendering della scala temporale (settimane o giorni)
  const renderTimeScale = () => {
    if (isWeekView) {
      return weeksList.map((week, index) => {
        const weekNumber = getWeekNumber(week);
        const width = getDateDiff(week, index < weeksList.length - 1 ? weeksList[index + 1] : endDate) * zoomLevel;
        return (
          <div key={index} style={{ 
            width: `${width}px`, 
            height: '20px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            borderRight: '1px solid #ccc',
            boxSizing: 'border-box',
            padding: '2px'
          }}>
            <span style={{ fontSize: '0.6rem', fontWeight: 'bold' }}>Sett. {weekNumber}</span>
          </div>
        );
      });
    } else {
      return daysList.map((day, index) => (
        <div key={index} style={{ 
          width: `${zoomLevel}px`, 
          height: '50px',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'flex-end',
          borderRight: '1px solid #ccc',
          boxSizing: 'border-box',
          padding: '1px'
        }}>
          <div style={{ 
            transform: 'rotate(-90deg)', 
            transformOrigin: 'center',
            fontSize: '0.6rem',
            whiteSpace: 'nowrap',
            marginBottom: '5px'
          }}>
            <span style={{ 
              fontWeight: 'bold',
              color: day.getDay() === 0 || day.getDay() === 6 ? 'red' : 'black'
            }}>
              {day.toLocaleString('it-IT', { weekday: 'short' })}
            </span>
          </div>
          <div style={{ fontSize: '0.6rem' }}>{day.getDate()}</div>
        </div>
      ));
    }
  };

  // Rendering dei divisori verticali nel grafico (settimane e anni)
  const renderDividers = () => {
    return (
      <>
        {(isWeekView ? weeksList : daysList).map((date, index) => (
          <div
            key={`divider-${index}`}
            style={{
              position: 'absolute',
              top: 0,
              bottom: 0,
              left: `${(getDateDiff(startDate, date) * zoomLevel)}px`,
              borderLeft: `1px solid ${isWeekView ? '#b3e0ff' : '#e6e6e6'}`,
              pointerEvents: 'none',
            }}
          ></div>
        ))}
        {yearsList.map((year, index) => (
          <div
            key={`year-${index}`}
            style={{
              position: 'absolute',
              top: 0,
              bottom: 0,
              left: `${(getDateDiff(startDate, year) * zoomLevel)}px`,
              borderLeft: '2px solid #ff6666',
              pointerEvents: 'none',
            }}
          ></div>
        ))}
      </>
    );
  };

  // Gestione del click su un task per aprire la finestra di modifica
  const handleTaskClick = (task) => {
    setEditingTask(task);
    setIsEditModalOpen(true);
  };

  // Funzione per aggiornare un task modificato
  const updateTask = () => {
    if (editingTask) {
      const updatedTasks = tasks.map(task => 
        task.id === editingTask.id ? editingTask : task
      );
      setTasks(updatedTasks);
      setIsEditModalOpen(false);
      setEditingTask(null);
      onUpdateTask(editingTask);
    }
  };

  // Rendering delle righe dei task nel grafico
  const renderTaskRows = () => {
    return tasks.map((task) => (
      <div key={task.id} style={{ 
        display: 'flex', 
        alignItems: 'center', 
        height: '20px',
        borderBottom: '1px solid #e6e6e6',
        position: 'relative'
      }}>
        <div style={{ 
          width: '340px',
          flexShrink: 0, 
          display: 'flex', 
          alignItems: 'center', 
          padding: '0 8px',
          position: 'sticky',
          left: 0,
          backgroundColor: '#f9fafb',
          zIndex: 2
        }}>
          <span style={{ 
            width: '30px',
            height: '18px',
            backgroundColor: '#3b82f6', 
            color: 'white', 
            borderRadius: '4px', 
            display: 'flex', 
            alignItems: 'center', 
            justifyContent: 'center', 
            marginRight: '8px',
            fontSize: '0.7rem'
          }}>
            {task.displayId}
          </span>
          <span style={{ 
            width: '120px',
            overflow: 'hidden', 
            textOverflow: 'ellipsis', 
            whiteSpace: 'nowrap', 
            marginRight: '8px', 
            fontSize: '0.6rem',
            color: '#333' 
          }}>{task.name}</span>
          <span style={{ 
            width: '90px',
            fontSize: '0.6rem',
            color: '#666', 
            marginRight: '8px' 
          }}>{formatDate(task.start)}</span>
          <span style={{ 
            width: '90px',
            fontSize: '0.6rem',
            color: '#666' 
          }}>{formatDate(task.end)}</span>
          <span style={{ 
            width: '60px',
            fontSize: '0.6rem',
            color: '#333',
          }}>
            {task.duration} giorni
          </span>
        </div>
        <div style={{ flex: 1, position: 'relative', height: '100%' }}>
          {renderDividers()}
          <div
            style={{
              position: 'absolute',
              top: '6px',
              height: '10px', 
              backgroundColor: '#3b82f6',
              borderRadius: '4px',
              cursor: 'pointer',
              left: `${getDateDiff(startDate, new Date(task.start)) * zoomLevel}px`,
              width: `${(getDateDiff(task.start, task.end) + 1) * zoomLevel}px`
            }}
            onClick={() => handleTaskClick(task)}
          ></div>
        </div>
      </div>
    ));
  };

  // Funzione per gestire lo scrolling
  const handleScroll = () => {
    if (timelineRef.current) {
      setScrollPosition(timelineRef.current.scrollTop);
    }
  };

  // Effetto per gestire lo scroll e gli eventi
  useEffect(() => {
    const timeline = timelineRef.current;
    if (timeline) {
      timeline.addEventListener('scroll', handleScroll);
    }

    return () => {
      if (timeline) {
        timeline.removeEventListener('scroll', handleScroll);
      }
    };
  }, []);

  const events = tasks.map(task => ({
    label: task.name,
    date: task.start
  }));

  return (
    <div style={{ padding: '16px' }}>

      {/* Header del diagramma di Gantt */}
      <div style={{ 
        width: '335px',
        marginBottom: '-30px', 
        display: 'flex', 
        justifyContent: 'space-between', 
        alignItems: 'center',
        padding: '10px',
        backgroundColor: '#ffffff',
        position: 'sticky', 
        zIndex: 10

      }}>
        <span style={{ fontWeight: 'bold' }}>Diagramma di Gantt</span>
        {/* Barra di zoom */}
        <div>
          <input 
            type="range" 
            min="5" 
            max="60" 
            value={zoomLevel} 
            onChange={handleZoomChange} 
          />
        </div>
      </div>

{/* Nuovo box sopra l'intestazione ---------------------------------------------------*/}
{/*<div style={{ 
      padding: '10px', 
      backgroundColor: '#f9fafb', 
      marginBottom: '-22px',
      position: 'sticky', // Mantiene il box visibile durante lo scrolling
      top: '40px',  // Posiziona il box in cima alla pagina
      zIndex: 10 // Assicura che sia sopra l'intestazione delle colonne
  }}>
    <span style={{ fontWeight: 'bold' }}>Contenuto Aggiuntivo</span>
    </div>*/}

      {/* Contenitore principale del diagramma di Gantt */}
      <div 
        ref={timelineRef}
        style={{ 
          overflowX: 'auto', 
          overflowY: 'auto',
          maxWidth: '100%', 
          maxHeight: '400px',
          marginBottom: '50px' // Spazio aggiuntivo sotto il grafico
        }}
      >
        
        <div style={{ minWidth: `${totalDays * zoomLevel + 340}px`, position: 'relative' }}>
          {/* Barra dei mesi */}
          <div style={{ 
            display: 'flex', 
            position: 'sticky', 
            top: 0, 
            zIndex: 4,  
            backgroundColor: '#ffffff',
            height: '40px', 
          }}>
            <div style={{ width: '340px', flexShrink: 0 }}></div>
            <div style={{ flex: 1, height: '20px' }}>
              {renderMonths()}
            </div>
          </div>

          {/* Intestazioni delle colonne */}
          <div style={{ 
            display: 'flex', 
            position: 'sticky', 
            top: '40px', 
            zIndex: 10, 
            backgroundColor: '#f9fafb', 
            height: '60px', // Altezza aumentata
            paddingTop: '0px', // Aggiunge spazio extra verso l'alto
            paddingBottom: '0px', // Mantiene lo spazio inferiore se necessario
            //boxSizing: 'border-box' // Garantisce che padding non aumenti la dimensione totale del box
          }}>
          <div style={{ 
            width: '337px', 
            flexShrink: 0, 
            display: 'flex', 
            alignItems: 'center', 
            padding: '10px 10px',
            fontWeight: 'bold',
            fontSize: '0.7rem',
            height: '40px', // Altezza della riga interna
            position: 'sticky',
            left: 0,
            backgroundColor: '#f9fafb',
            zIndex: 10
          }}>
        <span style={{ width: '30px', marginRight: '8px' }}></span>
        <span style={{ width: '120px', marginRight: '8px' }}>Descrizione attività</span>
        <span style={{ width: '90px', marginRight: '8px' }}>Data inizio</span>
        <span style={{ width: '90px' }}>Data fine</span>
        <span style={{ width: '60px', marginRight: '8px' }}>Durata</span>
    </div>
    {renderTimeScale()}
          </div>

          {/* Righe delle attività */}
          {renderTaskRows()}
        </div>
      </div>

      {/* Timeline */}
      <TimelineComponent events={events} startDate={startDate} endDate={endDate} />

      {/* Finestra modale per la modifica delle attività */}
      {isEditModalOpen && (
        <div style={{
          position: 'fixed',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundColor: 'rgba(0, 0, 0, 0.5)',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          zIndex: 1000
        }}>
          <div
            style={{
              backgroundColor: 'white',
              borderRadius: '8px',
              padding: '16px',
              zIndex: 1001,
              boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
              maxWidth: '600px', 
              maxHeight: '80%',
              width: 'auto', 
              height: 'auto',
              overflowY: 'auto',
            }}
          >
            <h2 style={{ marginBottom: '16px' }}>Modifica Task</h2>
            {editingTask && (
              <div style={{ display: 'grid', gap: '16px' }}>
                <div>
                  <label htmlFor="name" style={{ display: 'block', marginBottom: '4px' }}>Nome</label>
                  <input
                    id="name"
                    value={editingTask.name}
                    onChange={(e) => setEditingTask({ ...editingTask, name: e.target.value })}
                    style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ccc' }}
                  />
                </div>
                <div>
                  <label htmlFor="start" style={{ display: 'block', marginBottom: '4px' }}>Data Inizio</label>
                  <input
                    id="start"
                    type="date"
                    value={editingTask.start}
                    onChange={(e) => setEditingTask({ ...editingTask, start: e.target.value })}
                    style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ccc' }}
                  />
                </div>
                <div>
                  <label htmlFor="end" style={{ display: 'block', marginBottom: '4px' }}>Data Fine</label>
                  <input
                    id="end"
                    type="date"
                    value={editingTask.end}
                    onChange={(e) => setEditingTask({ ...editingTask, end: e.target.value })}
                    style={{ width: '100%', padding: '8px', borderRadius: '4px', border: '1px solid #ccc' }}
                  />
                </div>
              </div>
            )}
            <div style={{ marginTop: '20px', display: 'flex', justifyContent: 'flex-end' }}>
              <button
                onClick={updateTask}
                style={{
                  backgroundColor: '#3b82f6',
                  color: 'white',
                  padding: '8px 16px',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                Salva Modifiche
              </button>
              <button
                onClick={() => setIsEditModalOpen(false)}
                style={{
                  marginLeft: '8px',
                  backgroundColor: '#e5e7eb',
                  padding: '8px 16px',
                  border: 'none',
                  borderRadius: '4px',
                  cursor: 'pointer'
                }}
              >
                Annulla
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default GanttChartComponent;
