import React from 'react';
import { Line } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js';

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

const PercentageCurveComponent = ({ tableRows }) => {
  const data = {
    labels: [],
    datasets: [
      {
        label: 'Percentuale Cumulativa delle Ore',
        data: [],
        fill: false,
        borderColor: 'rgb(75, 192, 192)',
        tension: 0.1,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Curva Percentuale Cumulativa delle Ore',
      },
    },
    scales: {
      y: {
        beginAtZero: true,
        max: 100,
        title: {
          display: true,
          text: 'Percentuale Completamento (%)',
        },
      },
      x: {
        title: {
          display: true,
          text: 'Data',
        },
      },
    },
  };

  let totalOreMan = 0;
  const dateOreMap = new Map();

  // Distribuzione delle ore giornaliere e calcolo del totale delle ore
  tableRows.forEach((row) => {
    const oreMan = parseFloat(row.oreMan.replace(/\./g, '').replace(',', '.')) || 0;
    totalOreMan += oreMan;

    const startDate = new Date(row.dataInizio);
    const endDate = new Date(row.dataFine);
    const daysDiff = Math.ceil((endDate - startDate) / (1000 * 60 * 60 * 24)) + 1;
    const orePerDay = oreMan / daysDiff;

    let currentDate = new Date(startDate);
    while (currentDate <= endDate) {
      const dateStr = currentDate.toISOString().split('T')[0];
      const currentOre = dateOreMap.get(dateStr) || 0;
      dateOreMap.set(dateStr, currentOre + orePerDay);
      currentDate.setDate(currentDate.getDate() + 1);
    }
  });

  // Calcolo della percentuale cumulativa delle ore giorno per giorno
  let cumulativeOre = 0;
  Array.from(dateOreMap.entries())
    .sort(([dateA], [dateB]) => new Date(dateA) - new Date(dateB))
    .forEach(([date, ore]) => {
      cumulativeOre += ore;
      const percentage = (cumulativeOre / totalOreMan) * 100;
      data.labels.push(new Date(date).toLocaleDateString('it-IT', { day: '2-digit', month: '2-digit', year: 'numeric' }));
      data.datasets[0].data.push(percentage.toFixed(2));
    });

  return <Line options={options} data={data} />;
};

export default PercentageCurveComponent;
