import React from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, BarElement, CategoryScale, LinearScale, Title, Tooltip, Legend } from 'chart.js';

ChartJS.register(BarElement, CategoryScale, LinearScale, Title, Tooltip, Legend);

const HistogramComponent = ({ tableRows }) => {
  const data = {
    labels: [],
    datasets: [
      {
        label: 'Lavoratori per Giorno',
        data: [],
        backgroundColor: 'rgba(54, 162, 235, 0.6)',
        borderColor: 'rgba(54, 162, 235, 1)',
        borderWidth: 1,
      },
    ],
  };

  const workersPerDayMap = {};

  tableRows.forEach((row) => {
    const startDate = new Date(row.dataInizio);
    const endDate = new Date(row.dataFine);
    const totalOreMan = parseFloat(row.oreMan.replace(/\./g, '').replace(',', '.')) || 0;
    const daysDiff = Math.ceil((endDate - startDate) / (1000 * 60 * 60 * 24)) || 1;
    const workersPerDay = totalOreMan / (daysDiff * 8);

    let currentDate = new Date(startDate);
    while (currentDate <= endDate) {
      // Conversione della data nel formato italiano (gg-mm-aaaa)
      const dateStr = currentDate.toLocaleDateString('it-IT', { day: '2-digit', month: '2-digit', year: 'numeric' });
      if (!workersPerDayMap[dateStr]) {
        workersPerDayMap[dateStr] = 0;
      }
      workersPerDayMap[dateStr] += workersPerDay;
      currentDate.setDate(currentDate.getDate() + 1);
    }
  });

  // Ordinamento delle date e preparazione dei dati per il grafico
  const sortedDates = Object.keys(workersPerDayMap).sort(
    (a, b) => new Date(a.split('/').reverse().join('-')) - new Date(b.split('/').reverse().join('-'))
  );

  data.labels = sortedDates;
  data.datasets[0].data = sortedDates.map(date => workersPerDayMap[date]);

  return <Bar data={data} />;
};

export default HistogramComponent;
