// tableStyles.js

// Definizione degli stili per la tabella
const tableStyles = {
  container: {
    marginTop: '20px',
    overflowY: 'auto',
    maxHeight: '310px',
    overflowX: 'auto',
    border: '1px solid #ccc',
  },
  labelContainer: {
    display: 'flex',
    alignItems: 'center',
    marginBottom: '-19px',
    backgroundColor: 'transparent',
    padding: '8px',
    position: 'sticky',
    top: 0,
    zIndex: 2,
  },
  table: {
    width: '100%',
    borderCollapse: 'collapse',
    fontSize: '0.7rem',
    //border: '1px solid #ccc', // Aggiunge una cornice alla tabella
  },
  th: {
    border: '1px solid #ddd',
    padding: '8px',
    textAlign: 'left',
    backgroundColor: '#f2f2f2',
    fontWeight: 'bold',
    whiteSpace: 'nowrap',
    position: 'sticky',
    //border: '1px solid #ccc', // Aggiunge una cornice alle celle dell'intestazione
    top: 0,
    zIndex: 1,
  },
  td: {
    border: '1px solid #ddd',
    padding: '4px',
    fontSize: '0.7rem',
    //border: '1px solid #ccc', // Aggiunge una cornice alle celle dell'intestazione
    whiteSpace: 'nowrap',
  },
  input: {
    width: '100%',
    border: 'none',
    background: 'transparent',
    fontSize: '0.7rem',
    padding: '2px',
    margin: '0',
    outline: 'none',
    boxSizing: 'border-box',
  },
  icon: {
    cursor: 'pointer',
    color: '#3b82f6',
    marginRight: '50px',
    position: 'relative',
  },
  deleteIcon: {
    cursor: 'pointer',
    color: '#ef4444',
    marginLeft: '5px',
  },
};

// Esportazione degli stili della tabella per poterli usare in altri file
export default tableStyles;
